<template>
   <form
   id="formchercheur"
   @submit="checkForm"
   action=""
   method="post">

    <p v-if="errors.length">
        <b>Please correct the following error(s):</b>
        <ul>
        <li  v-for="error in errors" :key="error.message">{{ error }}</li>
        </ul>
    </p>
    <p>
    <label for="name">Name</label>
    <input
      id="name"
      v-model="name"
      type="text"
      name="name"
    >
  </p>

  <p>
    <label for="age">Age</label>
    <input
      id="age"
      v-model="age"
      type="number"
      name="age"
      min="0"
    >
  </p>

  <p>
    <label for="movie">Favorite Movie</label>
    <select
      id="movie"
      v-model="movie"
      name="movie"
    >
      <option>Star Wars</option>
      <option>Vanilla Sky</option>
      <option>Atomic Blonde</option>
    </select>
  </p>

  <p>
    <input
      type="submit"
      value="Submit"
    >
  </p>

   </form> 

</template>

<script>


export default {
  name: 'chercheur',
  props: {
  },
  data(){
      return {
          form:{
                errors: [],
                name:'',
                age: null,
                movie: null
          }
          
      }

  },


}
    
</script>